<template>
    <input type="file" @change="handleFileChange"><br>
    <text> rows {{ import_file }}</text><br>
</template>

<script>
export default {
    data() {
        return {
            hasifot: [],
            history: [],
            documents: [],
        }
    },
    props:{
        import_file:{
            type: String,
            required: true
        }
    },
    methods: {
        handleFileChange(event) {
            const file = event.target.files[0];
            if (file) { 
                this.readFile(file);
            }
        },
        readFile(file) {
            const reader = new FileReader();
            reader.onload = () => {
                const csvData = reader.result;
                const decoder = new TextDecoder('windows-1255');
                const utf8Content = decoder.decode(new Uint8Array(csvData));
                const dsData = this.parseCSV(utf8Content);
                if(this.import_file === 'Hasifa'){
                    this.hasifot = dsData;
                } else {
                    if(this.import_file === 'History'){
                        this.history = dsData;
                    } 
                }
            };
            reader.readAsArrayBuffer(file);
        },
        parseCSV(csvData) {
            const lines = csvData.split('\n');
            const dsData = [];
            const jsonData = [];
            if(this.import_file === 'Hasifa'){
                for (let i = 1; i < lines.length; i++) {
                    let values = lines[i].split(',');
                    dsData.push(values);
                    jsonData.push({
                        PROD_ID: values[0],
                        SUG: values[1],
                        ID: values[2],
                        SUG_MUZAR: values[3],
                        SHM_MUZAR: values[4],
                        MISPAR_HEVRA: values[5],
                        SHM_HEVRA: values[6],
                        YITRAT_NEHASIM: values[7],
                        MENAYOT: values[8],
                        AGAH_CONC: values[9],
                        AGAH_MMSL: values[10],
                        HALVAAOT_LS: values[11],
                        KRANOT_LS: values[12],
                        AHER: values[13],
                        MEZUMAN_PIKDONOT: values[14],
                        MATAH: values[15],
                        HUL: values[16]
                    })
                }
                //this.UploadDB(jsonData, 'hasifa');
                console.log(jsonData);
                this.$store.state.count = dsData.length;
                this.$store.state.hasiDB = dsData;
            } else {
                if(this.import_file === 'History'){
                    for (let i = 1; i < lines.length; i++) {
                        let values = lines[i].split(',');
                        dsData.push(values);
                        jsonData.push({
                            PROD_ID: values[0],
                            DATE: values[6],
                            YIELD: values[7],
                            STD_12: values[8],
                            STD_36: values[9],
                            STD_60: values[10]
                        })
                    }
//                    this.UploadDB(jsonData, 'yield');
                    console.log(jsonData);
                    this.$store.state.hist_count = dsData.length;
                    this.$store.state.historyDB = dsData;
                }
            }
            return dsData;

        },
//        async UploadDB(json, place){
//            const result = await post(`${this.baseUrl}//rstsrv/{place}/update`,json);
//            console.log(result);
//        },
    },
}
</script>

